<template>
  <div id="">
    <!--  -->
    <el-card>
      <div class="deadline">
        <div class="deadline-info">{{ deadline }}</div>
        <el-date-picker
          ref="deadlinePickerRef"
          v-model="newDeadline"
          type="datetime"
          placeholder=""
          :editable="false"
          :picker-options="deadlinePickerOptions"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
        >
        </el-date-picker>
        <el-popconfirm
          title=""
          @confirm="editDeadline"
        >
          <el-button slot="reference"></el-button>
        </el-popconfirm>
      </div>
      <el-input
        maxlength="20"
        v-model.trim="searchAdminsValue"
        @input="searchChange"
        @keyup.enter.native="searchAdmins"
        placeholder=""
        class="input-with-select"
        clearable
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchAdmins"
        ></el-button>
      </el-input>
      <el-button @click="addAdmin"></el-button>

      <!--  -->
      <el-table border stripe :data="renderAdminList">
        <!-- <el-table-column label="id" prop="id"></el-table-column> -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="password"></el-table-column>
        <el-table-column label="" prop="role"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="operation-admin">
              <el-button
                type="primary"
                icon="el-icon-edit"
                @click="editAdmin(scope.row)"
              ></el-button>
              <el-popconfirm
                confirm-button-text=""
                cancel-button-text=""
                icon="el-icon-info"
                icon-color="red"
                title="?"
                @confirm="deleteAdmin(scope.row)"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  v-if="scope.row.role != 'root'"
                  slot="reference"
                ></el-button>
              </el-popconfirm>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- / dialog Form -->
    <el-dialog
      center
      :close-on-click-modal="false"
      :title="dialogInfo.title"
      :visible.sync="dialogInfo.editAdminDialogVisible"
      @close="closeDialog"
    >
      <el-form
        :model="editAdminForm"
        :rules="addAdminRules"
        ref="addAdminFormRef"
      >
        <el-form-item
          label=""
          prop="username"
          :label-width="dialogInfo.editAdminDialogLabelWidth"
        >
          <el-input
            v-model.trim="editAdminForm.username"
            maxlength="20"
            @input="handleAdminFormChange"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="password"
          :label-width="dialogInfo.editAdminDialogLabelWidth"
        >
          <el-input
            v-model.trim="editAdminForm.password"
            maxlength="20"
            @input="handleAdminFormChange"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleDialogCancel"> </el-button>
        <el-button type="primary" @click="handleDialogSubmit('addAdminFormRef')"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {modifyAdminInfo} from '../../network/admin'
export default {
  name: "",
  data() {
    return {
      adminList: [], // 
      renderAdminList: [], // 
      searchAdminsValue: "", // 
      editAdminForm: {
        // /
        id: "",
        username: "",
        password: "",
      },
      temEditAdminInfo: {}, // 
      dialogInfo: {
        // dialogInfo 
        operation: "", // 1 2 
        title: "",
        editAdminDialogVisible: false,
        editAdminDialogLabelWidth: "70px",
      },
      addAdminRules: {
        // 
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^([0-9]|[a-z])*$/i,
            message: "",
            trigger: "blur",
          },
          {
            pattern: /^([0-9]|[a-z])*$/i,
            message: "",
            trigger: "change",
          },
          { min: 4, message: "", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^([0-9]|[a-z]|[\*]|[\.])*$/i,
            message: "",
            trigger: "change",
          },
          { min: 6, message: "", trigger: "blur" },
        ],
      },
      deadline: "",
      newDeadline: "",
      deadlinePickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 31);
              picker.$emit("pick", date);
            },
          },
        ],
      },
      popConfirmTitle: "",
    };
  },
  methods: {
    // 
    getAdmins() {
      // get
      modifyAdminInfo.getAdmins()
        .then((res) => {
          if (!res.state) {
            // 
            this.$message.error(res.msg);
            return;
          }
          // 
          this.adminList = res.data;
          this.renderAdminList = res.data;
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    // 
    getDeadline() {
      // get
      modifyAdminInfo.getDeadline()
        .then((res) => {
          if (!res.state) {
            // 
            this.$message.error(res.msg);
            return;
          }
          this.deadline = res.deadline;
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    // 
    editDeadline() {
      console.log("");
      // 
      if (!this.newDeadline) {
        this.$message.warning("");
        return;
      }
      // 
      let formData = new FormData();
      formData.append("deadline", this.newDeadline);
      // put
      modifyAdminInfo.setDeadline(formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        }
      })
        .then((res) => {
          if (!res.state) {
            // 
            this.$message.error(res.msg);
            return;
          }
          // 
          this.$message.success(res.msg);
          this.getDeadline();
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    // 
    searchChange(searchAdminsValue) {
      // 
      if (searchAdminsValue == "" && this.renderAdminList != this.adminList) {
        this.renderAdminList = this.adminList;
      }
    },
    // 
    searchAdmins() {
      this.renderAdminList = [];
      let reg = new RegExp(this.searchAdminsValue, "g");
      this.adminList.forEach((item) => {
        if (reg.test(item.username)) {
          this.renderAdminList.push(item);
        }
      });
    },
    // 
    addAdmin() {
      //  Dialog 
      this.dialogInfo.operation = "1";
      this.dialogInfo.title = "";
      this.dialogInfo.editAdminDialogVisible = true;
    },
    // 
    editAdmin(adminInfo) {
      this.temEditAdminInfo = adminInfo; // 
      //  Dialog 
      this.dialogInfo.operation = "2";
      this.dialogInfo.title = "";
      this.editAdminForm.username = adminInfo.username;
      this.editAdminForm.password = adminInfo.password;
      this.dialogInfo.editAdminDialogVisible = true;
      //  id
      this.editAdminForm.id = adminInfo.id;
    },
    // 
    deleteAdmin(adminInfo) {
      let formData = new FormData();
      formData.append("id", adminInfo.id);
      // delete
      modifyAdminInfo.deleteAdmin()
        .then((res) => {
          if (!res.state) {
            // 
            this.$message.error(res.msg);
            return;
          }
          this.$message.success("");
          // 
          this.getAdmins();
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    // Dialog /
    handleDialogSubmit(formName) {
      let addAdminValid = false;
      this.$refs[formName].validate((valid) => {
        addAdminValid = valid;
        console.log(valid);
      });
      if (!addAdminValid) {
        return;
      }
      let editAdminForm = this.editAdminForm;
      let formData = new FormData();
      if (this.dialogInfo.operation == "1") {
        //  1  2 
        // 
        formData.append("username", editAdminForm.username);
        formData.append("password", editAdminForm.password);
        // post
        modifyAdminInfo.addAdmin(formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          }
        })
          .then((res) => {
            if (!res.state) {
              // 
              this.$message.error(res.msg);
              return;
            }
            this.$message.success("");
            //  Dialog
            this.dialogInfo.editAdminDialogVisible = false;
            // 
            this.getAdmins();
          })
          .catch((err) => {
            console.log(err);
            this.$message.error("");
          });
      } else if (this.dialogInfo.operation == "2") {
        // 
        let temEditAdminInfo = this.temEditAdminInfo;
        //  if 
        if (
          temEditAdminInfo.username == editAdminForm.username &&
          temEditAdminInfo.password == editAdminForm.password
        ) {
          this.$message.warning("");
          return;
        }
        formData.append("username", editAdminForm.username);
        formData.append("password", editAdminForm.password);
        formData.append("id", this.editAdminForm.id);
        // put
        modifyAdminInfo.updateAdmin(formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          }
        })
          .then((res) => {
            if (!res.state) {
              // 
              this.$message.error(res.msg);
              return;
            }
            this.$message.success("");
            //  Dialog
            this.dialogInfo.editAdminDialogVisible = false;
            // 
            this.getAdmins();
          })
          .catch((err) => {
            console.log(err);
            this.$message.error("");
          });
      } else {
        this.$message.error("");
      }
    },
    // 
    handleAdminFormChange(e) {
      console.log(e);
      let reg = /[a-z]|[A-Z]|[0-9]/g;
    },
    // Dialog 
    handleDialogCancel() {
      this.dialogInfo.editAdminDialogVisible = false;
    },
    // Dialog 
    closeDialog() {
      this.editAdminForm.id = "";
      this.editAdminForm.username = "";
      this.editAdminForm.password = "";
    },
  },
  // 
  beforeRouteEnter(to, from, next) {
    let role = window.localStorage.getItem("role");
    if (role == "root") {
      return next();
    }
    next("/QueryByGroup");
  },
  created() {
    this.getAdmins(); // 
    this.getDeadline(); // 
  },
};
</script>

<style lang='less' scoped>
@import "../../assets/css/children/AddAdmin.less";
</style>