<template>
  <div id="">
    <!--  -->
    <el-card>
      <!--  -->
      <el-input
        maxlength="10"
        v-model.trim="searchByStuIdValue"
        @input="searchChange"
        @keyup.enter.native="searchResumeByStuId"
        placeholder=""
        clearable
      >
        <!--  -->
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchResumeByStuId"
        ></el-button>
      </el-input>
      <!--  -->
      <el-table border stripe :data="resumeList">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="studentId"></el-table-column>
        <el-table-column label="" prop="phone"></el-table-column>
        <el-table-column label="" prop="grade"></el-table-column>
        <el-table-column label="" width="310%">
          <template slot-scope="scope">
            <span
              class="intentsion-group"
              v-for="item in scope.row.intensionGroup"
              :key="item.resumeId"
            >
              <el-tag type="success" disable-transitions>{{ item }}</el-tag>
            </span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="resume-state">
              <div v-if="scope.row.adjustment == ''" style="color: #67c23a">
                
              </div>
              <div
                v-else-if="scope.row.adjustment == ''"
                style="color: #f56c6c"
              >
                
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="resume-state">
              <div
                :style="{
                  color:
                    scope.row.resumeState == '' ||
                    scope.row.resumeState == '' ||
                    scope.row.resumeState == ''
                      ? '#f56c6c'
                      : '#67c23a',
                }"
              >
                {{ scope.row.resumeState }}
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span @click="transmitToParent(scope.row)" class="resume-detail"
              ></span
            >
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-popconfirm
              confirm-button-text=""
              cancel-button-text=""
              icon="el-icon-info"
              icon-color="red"
              title="?"
              @confirm="deleteResume(scope.row)"
            >
              <span class="resume-delete" slot="reference"></span>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="pagination">
        <el-pagination
          hide-on-single-page
          :page-size="10"
          :page-count="paginationInfo.pageCount"
          :current-page="paginationInfo.currentPage"
          @current-change="handlePagesChange"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import {queryResumeMethods, deleteResumeById} from '../../network/admin'
export default {
  name: "",
  data() {
    return {
      resumeList: [], // 
      searchByStuIdValue: "", // 
      paginationInfo: {
        // 
        pageCount: 1, //  1
        currentPage: 1, // 
      },
    };
  },
  methods: {
    // 
    searchChange() {},
    // 
    searchResumeByStuId() {
      let queryInfo = {
        stuId: this.searchByStuIdValue,
        page: this.paginationInfo.currentPage,
      };
      // get
      queryResumeMethods.queryByStuId(queryInfo)
        .then((res) => {
          if (!res.state) {
            this.$message.error(res.msg);
            return;
          }
          // 
          // 
          let intensionGroupValueArr = ["", "", "", "", ""];
          let resumeStateValueArr = [
            "",
            "",
            "",
            "",
            "",
          ];
          res.data = res.data.map((item1) => {
            //  return 
            // item1.intensionGroup = JSON.parse(item1.intensionGroup);
            item1.intensionGroup = item1.intensionGroup.split(",");
            //  intensionGroup  
            item1.intensionGroup = item1.intensionGroup.map((item2) => {
              item2 = intensionGroupValueArr[item2 - 1];
              return item2;
            });
            console.log(item1.resumeState);
            //  resumeState  
            item1.resumeState = resumeStateValueArr[item1.resumeState];
            return item1;
          });
          this.resumeList = res.data;
        })
        .catch((err) => {
          this.$message.error("");
          console.log(err);
        });
    },
    // 
    transmitToParent(resumeInfo) {
      this.$emit("transmitResumeInfo", resumeInfo); //  Manager 
      this.$router.push({ name: "ExamineResume", params: { state: true } });
    },
    // 
    async deleteResume(resumeInfo) {
      let queryInfo = {
        id: resumeInfo.id,
      };
      // delete
      deleteResumeById(queryInfo)
        .then((res) => {
          if (!res.state) {
            this.$message.error(res.msg);
            return false;
          }
          this.$message.success(res.msg);
          this.searchResumeByStuId()
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    // 
    handlePagesChange(currentPage) {
      this.paginationInfo.currentPage = currentPage;
      // 
      this.searchResumeByStuId();
    },
  },
  created() {
    this.searchResumeByStuId();
  },
};
</script>

<style lang='less' scoped>
@import "../../assets/css/global.less";
@import "../../assets/css/children/ManagerPublic.less";
</style>