let frequentlyCounter = 0;
function frequentlyCount(_this) {// 15  6 
  frequentlyCounter++;
  if (frequentlyCounter > 6) {
    _this.$message.warning("");
    return true;
  }
  if (frequentlyCounter == 1) {
    setTimeout(() => {
      frequentlyCounter = 0;
    }, 15 * 1000);
  }
  // clearTimeout(changeCodeTimtOut);
  // changeCodeTimtOut = 
  return false;
}

// 
function throttle(fn, delay = 300) {
  let timer = null;
  let timeStamp = new Date();
  return () => {
    let args = arguments;  //
    if (new Date() - timeStamp > delay) {
      timeStamp = new Date();
      timer = setTimeout(function () {
        fn.apply(this, args);
      }, delay);
    }

  }
}

export default {
  //
  frequentlyCount,
  throttle
}