<template>
  <div id="">
    <el-card>
      <div class="SMS-tips">
        <div class="text-info"></div>
        <div class="text-tip">
          <br />
          <br />
          <strong></strong><strong>"&lt;name&gt;"</strong><br />
          <p><strong>"&lt;software&gt;"</strong></p>
          <p><strong>"&lt;hardware&gt;"</strong></p>
          <p><strong>"&lt;design&gt;"</strong></p>
          <p><strong>"&lt;algorithm&gt;"</strong></p>
          <p><strong>"&lt;union&gt;"</strong></p>
        </div>
      </div>
      <el-form
        class="form-tem"
        label-position="right"
        label-width="120px"
        :model="SMSTemplateData"
        :rules="SMSTemplateFormRules"
        ref="SMSTemplateFormRef"
      >
        <el-form-item label="" prop="type">
          <el-select
            class="tem-type-select"
            v-model.trim="SMSTemplateData.type"
            placeholder=""
            size="medium"
            style="width: 229px"
          >
            <el-option
              v-for="item in SMSTemplateOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input
            class="tem-title-input"
            placeholder=""
            v-model="SMSTemplateData.title"
            maxlength="100"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input
            class="tem-content-input"
            type="textarea"
            placeholder=""
            :autosize="{ minRows: 2, maxRows: 4 }"
            v-model="SMSTemplateData.content"
            maxlength="1000"
            show-word-limit
          >
          </el-input>
        </el-form-item>
        <el-button class="SMS-template-save" @click="saveSMSTemplate"
          ></el-button
        >
      </el-form>
      <div class="SMS-template">
        <div class="SMS-title"></div>
        <el-collapse v-model="activeNames">
          <el-collapse-item
            v-for="(item, index) in SMSTemplateList.interviewSuccess"
            :key="item.id"
            :title="item.title"
            :name="'1-' + index"
          >
            <div>{{ item.content }}</div>
            <div class="button-delete">
              <el-popconfirm
                confirm-button-text=""
                cancel-button-text=""
                icon="el-icon-info"
                icon-color="red"
                title=""
                @confirm="handleDelete('0', item.id)"
              >
                <el-button size="mini" type="danger" slot="reference"
                  ></el-button
                >
              </el-popconfirm>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <div class="SMS-template">
        <div class="SMS-title"></div>
        <el-collapse v-model="activeNames">
          <el-collapse-item
            v-for="(item, index) in SMSTemplateList.interviewFailed"
            :key="item.id"
            :title="item.title"
            :name="'2-' + index"
          >
            <div>{{ item.content }}</div>
            <div class="button-delete">
              <el-popconfirm
                confirm-button-text=""
                cancel-button-text=""
                icon="el-icon-info"
                icon-color="red"
                title=""
                @confirm="handleDelete('0', item.id)"
              >
                <el-button size="mini" type="danger" slot="reference"
                  ></el-button
                >
              </el-popconfirm>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
  </div>
</template>

<script>
import { api_SMSTemplate } from "../../network/admin";

export default {
  name: "",
  data() {
    return {
      SMSValue: "",
      activeNames: [],
      SMSTemplateOptions: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
      ],
      SMSTemplateData: {
        type: "",
        title: "",
        content: "",
      },
      SMSTemplateList: {
        interviewSuccess: [],
        interviewFailed: [],
      },
      //
      SMSTemplateFormRules: {
        type: [{ required: true, message: "", trigger: "blur" }],
        title: [{ required: true, message: "", trigger: "blur" }],
        content: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    // 
    saveSMSTemplate() {
      this.$refs["SMSTemplateFormRef"].validate(async (valid) => {
        if (!valid) {
          this.$message.success("");
          return false;
        }
        // let formData = new FormData();
        let { SMSTemplateData } = this;
        let newObj = {
          type: SMSTemplateData.type,
          title: SMSTemplateData.title,
          content: SMSTemplateData.content,
        };
        try {
          let res = await api_SMSTemplate.saveTemplate(newObj);
          if (!res.state) throw new Error(res.msg);
          this.$message.success(res.msg);
          this.SMSTemplateData.type = "";
          this.SMSTemplateData.title = "";
          this.SMSTemplateData.content = "";
          this.getSMSTemplate();
        } catch (err) {
          console.log(err);
          this.$message.error("");
        }
        // if (SMSTemplateData.type == "0") {
        //   this.SMSTemplateList.interviewSuccess.push(newObj);
        // } else {
        //   this.SMSTemplateList.failed.push(newObj);
        // }
        // this.$message.success("");
        // this.SMSTemplateList.interviewSuccess.map((item) => {
        //   item.content = item.content.replace(/<name>/g, "xxx");
        //   return item;
        // });
      });
    },
    // 
    async handleDelete(type, id) {
      const data = {
        id,
      };
      try {
        let res = await api_SMSTemplate.deleteTemplate(data);
        console.log(res);
        this.$message.success("");
      } catch (err) {
        this.$message.error("");
      }
      this.getSMSTemplate();
    },
    // 
    async getSMSTemplate() {
      try {
        let res = await api_SMSTemplate.getTemplates();
        if (!res.state) throw Error("");
        res = res.data;
        this.SMSTemplateList.interviewSuccess = res.interviewSuccess;
        this.SMSTemplateList.interviewFailed = res.interviewFailed;
      } catch (err) {
        this.$message.error("");
        console.log(err);
      }
    },
  },
  mounted() {
    this.getSMSTemplate();
  },
};
</script>

