<template>
  <div id="">
    <!--  -->
    <el-card>
      <!--  -->
      <ButtonTabs @buttonTabs="handleButtonTabs" :buttonTabsList="buttonTabsList" :index="index"></ButtonTabs>
      <!--  -->
      <el-table border stripe :data="resumeList" >
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="name" show-overflow-tooltip></el-table-column>
        <el-table-column label="" prop="studentId"></el-table-column>
        <el-table-column label="" prop="phone"></el-table-column>
        <el-table-column label="" prop="grade"></el-table-column>
        <el-table-column label="" width="310%">
          <template slot-scope="scope">
            <span
              class="intentsion-group"
              v-for="item in scope.row.intensionGroup"
              :key="item.resumeId"
            >
              <el-tag type="success" disable-transitions>{{ item }}</el-tag>
            </span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="resume-state">
              <div v-if="scope.row.adjustment == ''" style="color: #67c23a">
                
              </div>
              <div
                v-else-if="scope.row.adjustment == ''"
                style="color: #f56c6c"
              >
                
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="resume-state">
              <div :style="{color: scope.row.resumeState==''||scope.row.resumeState==''||scope.row.resumeState == ''?'#f56c6c':'#67c23a'}">
                {{scope.row.resumeState}}
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span @click="transmitToParent(scope.row)" class="resume-detail"
              ></span
            >
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-popconfirm
              confirm-button-text=""
              cancel-button-text=""
              icon="el-icon-info"
              icon-color="red"
              title="?"
              @confirm="deleteResume(scope.row)"
            >
              <span class="resume-delete" slot="reference"></span>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="pagination">
        <el-pagination
          hide-on-single-page
          @current-change="handlePagesChange"
          :current-page="paginationInfo.currentPage"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="paginationInfo.total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import ButtonTabs from "../../components/ButtonTabs.vue";
export default {
  components: {
    ButtonTabs
  },
  data() {
    return {
      SMSState: "0",
      index: "0",
      resumeList: [],
      buttonTabsList: ["","",""],
      paginationInfo: {// 
        pageCount: 1,//  1
        currentPage: 1,// 
        total: 1,
      },
    };
  },
  methods: {
    // ButtonTabs 
    handleButtonTabs(index) {
      // 
      this.paginationInfo.currentPage = 1;
      this.SMSState = index;
      this.queryBySMSState();
    },
    // 
    queryBySMSState() {
      let queryInfo = {
        SMSState:this.SMSState,
        page: this.paginationInfo.currentPage,
      };
      this.$axios({
        method: "get",
        url: "http://8.129.163.145:10012/admin/queryBySMSState",
        params: queryInfo,
      })
        .then((res) => {
          res = res.data;
          if (!res.state) {
            this.$message.error(res.msg);
            return;
          }
          // 
          // 
          let intensionGroupValueArr = ["", "", "", "", ""];
          let resumeStateValueArr = [
            "",
            "",
            "",
            "",
            '',
          ];
          res.data = res.data.map((item1) => {
            //  return 
            // item1.intensionGroup = JSON.parse(item1.intensionGroup);
            item1.intensionGroup = item1.intensionGroup.split(",");
            //  intensionGroup  
            item1.intensionGroup = item1.intensionGroup.map((item2) => {
              item2 = intensionGroupValueArr[item2 - 1];
              return item2;
            });
            //  resumeState  
            item1.resumeState = resumeStateValueArr[item1.resumeState];
            return item1;
          });
          this.resumeList = res.data;
          this.paginationInfo.pageCount = res.pages;
          this.paginationInfo.total = res.total
        })
        .catch((err) => {
          this.$message.error("");
          console.log(err);
        });
    },
    // 
    transmitToParent(resumeInfo) {
      this.$emit('transmitResumeInfo', resumeInfo);//  Manager 
      this.$router.push({name:"ExamineResume",params: {state: true}, query: {currentPage: this.paginationInfo.currentPage, SMSState: this.SMSState, total: this.paginationInfo.total}});
    },
    // 
    deleteResume(resumeInfo) {
      let queryInfo = {
        id: resumeInfo.id,
      };
      this.$axios({
        method: "delete",
        url: "http://8.129.163.145:10012/admin/deleteResume",
        params: queryInfo,
      })
        .then((res) => {
          res = res.data;
          if (!res.state) {
            this.$message.error(res.msg);
            return false;
          }
          this.$message.success(res.msg);
          this.queryBySMSState();
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    // 
    handlePagesChange(currentPage) {
      this.paginationInfo.currentPage = currentPage;
      // 
      this.queryBySMSState();
    },
  },
  // created() {
  //   this.queryBySMSState();
  // },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.name === 'ExamineResume') {
        const {SMSState, currentPage, total} = from.query
        vm.SMSState = SMSState
        vm.index = SMSState
        vm.paginationInfo.currentPage = currentPage
        vm.paginationInfo.total = total
      }
      vm.queryBySMSState();
    });
  }
};
</script>

<style lang='less' scoped>
@import "../../assets/css/global.less";
@import "../../assets/css/children/ManagerPublic.less";
</style>