<template>
  <div id="">
    <el-card>
      <!--  -->
      <el-table border stripe :data="logsList">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="time"></el-table-column>
        <el-table-column label="" prop="info"></el-table-column>
      </el-table>
      <!--  -->
      <div class="pagination">
        <el-pagination
          hide-on-single-page
          :page-size="20"
          :page-count="paginationInfo.pageCount"
          :current-page="paginationInfo.currentPage"
          @current-change="handlePagesChange"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import {getLog} from '../../network/admin'
export default {
  name: "",
  data() {
    return {
      paginationInfo: {
        // 
        pageCount: 1, //  1
        currentPage: 1, // 
      },
      logsList: [],
    };
  },
  methods: {
    // 
    queryLogs() {
      let queryInfo = {
        page: this.paginationInfo.currentPage
      };
      // get
      getLog(queryInfo)
        .then((res) => {
          if (!res.state) {
            this.$message.error(res.msg);
            console.log(res.msg);
            return false;
          }
          this.paginationInfo.pageCount = res.pages;
          this.logsList = res.logs;
        })
        .catch((err) => {
          console.log(err);
          this.$message.error('')
        });
    },
    // 
    handlePagesChange(currentPage) {
      this.paginationInfo.currentPage = currentPage;
      // 
      this.queryLogs();
    },
  },
  created() {
    this.queryLogs();
  },
};
</script>

<style lang='less' scoped>
@import "../../assets/css/global.less";
@import "../../assets/css/children/ManagerPublic.less";
</style>