        this.codeData = res;
        if (!res.state) {
          this.$message.error(res.msg);
          return;
        }
        this.resumeData.codeKey = res.codeKey;
        this.codeImgString = res.imgString;
      } catch (err) {
        this.$message.error("change CodeImg error");
      }
